import { Component, OnInit, ViewChild } from '@angular/core';
import {AlertingService} from '../../services/alerting.service';
import { DummyComp1 } from '../dummyComp1';
import { DummyComp2 } from '../dummyComp2';
import { DynamicComp } from 'src/app/dynamic-comp';
import { ComponentFactoryResolver } from '@angular/core';
@Component({
  selector: 'app-hello-world',
  templateUrl: './hello-world.component.html',
  styleUrls: ['./hello-world.component.css']
})
export class HelloWorldComponent implements OnInit {

  messageFromComponent : String = 'hello owrld';
  myId : String = 'randomId';
  bgColor : String = 'green';
  showMe : boolean = true;
  todoValue : String = 'type something'
  items : Array<String> = [];
  compArr = [DummyComp1, DummyComp2];
  imgURL = "../../../assets/ScheduleHealth.JPG";
  parentCls : String[] = ['parent'];
  redBorderApply = true;


  @ViewChild(DynamicComp, {static : true}) dynamicComp!:DynamicComp;

  constructor(private serv : AlertingService,
    private componentFactoryResolver : ComponentFactoryResolver) {
    this.messageFromComponent = 'message from outer world';
  }

  ngOnInit(): void {
    
  }

  divClick() : void {
    this.serv.show('I am a special div, green !!');
    this.showMe = !this.showMe;

    let viewRef = this.dynamicComp.viewRef;
    viewRef.clear();
    let compFactory = this.componentFactoryResolver.resolveComponentFactory(this.compArr[0]);
    viewRef.createComponent(compFactory);
  }

  addMe() : void{
    this.items.push(this.todoValue);
  }
  onChildEv() : void {
    this.serv.show('I am a comng from child comp !!');
  }
}
